// 自定义变量
//颜色
$body-bg:#f4f5f5;
$white: #fff;
$primary:#1e80ff;
$success: #6cbd45;


$text-color:#909090;
$text-search:#666;
$icon-color:#b2bac2;
$border-color:#f1f1f1;
$box-shadow: rgb(0 0 0 / 5%);
$typehead-color:#869aab; //搜索记录
$typehead-list-color:#5e6369; //搜索记录
$title:#2e3135;



// 大小
$header-h: 61px;
$header-btn-h:32px;
$nav-h:45px;
$main-top:15px;
// 全局混入

//单行文本溢出
@mixin nowrap {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

//指定行数溢出
@mixin clamp($num) {
  display: -webkit-box;
  -webkit-line-clamp: $num;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  overflow: hidden;
}

//禁止选中
@mixin no-select {
  -moz-user-select: none;
  /*火狐*/
  -webkit-user-select: none;
  /*webkit浏览器*/
  -ms-user-select: none;
  /*IE10*/
  -khtml-user-select: none;
  /*早期浏览器*/
  -o-user-select: none;
  /* Opera*/
  user-select: none;
}


// 图片
@mixin imgFit($fit:cover) {
  width: 100%;
  height: 100%;
  object-fit: $fit;
  vertical-align: top;
}

//去除元素移动端点击的背景色
@mixin no-highlight {
  -webkit-text-size-adjust: none;
  -webkit-tap-highlight-color: transparent;
}
// 全局方法
// 重置样式
:root {
  box-sizing: border-box;
  font-family: system-ui,
    -apple-system,
    BlinkMacSystemFont,
    segoe ui,
    Roboto,
    Helvetica,
    Arial,
    sans-serif;
}

*,
::before,
::after {
  box-sizing: inherit;
  font-family: inherit;
}

html,
body {
  margin: 0;
  padding: 0;
  font-size: 12px;
  color: #333;
  background-color: $body-bg;
}

body {
  overflow-y: scroll;
}

.container {
  max-width: 960px;
  margin: auto;

  @media screen and (max-width:980px) {
    width: 96%;
  }
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}


// 去除谷歌自动填充input样式-颜色覆盖
input:-webkit-autofill {
  box-shadow: 0 0 0px 1000px white inset;
}

//通知自定义字符图标
.notyf-icon {
  font-size: 21px;
}


//折叠动画
.mu-category-collapse {
  transition: height .25s;
}
//header
@import "@/assets/scss/default/header.scss";
@import "@/assets/scss/default/nav.scss";
@import "@/assets/scss/default/login-dialog.scss";
@import "@/assets/scss/default/fixed-tool.scss";
@import "@/assets/scss/comments/index.scss";


#main {
  padding-top: calc(#{$header-h} + #{$nav-h} + #{$main-top});

  .links-content {
    padding: 24px;
    padding-bottom: 36px;
    border-radius: 2px;
    background-color: $white;

    .links-head {
      margin-top: 30px;
      margin-bottom: 30px;

      .title {
        text-align: center;
        font-size: 32px;
        font-weight: lighter;
        color: $title;

        a {
          text-decoration: none;
          margin-left: 10px;
          font-size: 20px;
          color: $success;

          &:hover,
          &:active {
            opacity: .7;
          }
        }
      }
    }

    .links-body {

      ul,
      ol {
        list-style: none;
        margin: 0;
        padding: 0;
        margin: 30px 0 60px 0;
        display: grid;
        grid-template-columns: repeat(auto-fill, 212px);
        grid-auto-rows: 84px;
        grid-gap: 16px;

        @media screen and (max-width: 760px) {
          grid-template-columns: repeat(auto-fill, calc(33% - 11px));
        }

        @media screen and (max-width: 480px) {
          grid-template-columns: repeat(auto-fill, calc(50% - 8px));
        }

        @media screen and (max-width: 360px) {
          grid-template-columns: repeat(auto-fill, 100%);
        }

        li {
          padding: 5px;
          background-color: $white;
          box-shadow: inset 0 0 0 1px $border-color;
          position: relative;
          overflow: hidden;

          &:hover {
            box-shadow: 0 0 0 2px lighten($primary, 10%) inset;

            a {
              opacity: 1;
            }

            img {
              opacity: 0;
            }
          }

          a {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            padding: 10px 20px;
            // background-color: #fff;
            color: $title;
            font-size: 14px;
            line-height: 1.5;
            text-decoration: none;
            display: flex;
            justify-content: center;
            align-items: center;
            opacity: 0;
            transition: opacity .5s;
          }

          img {
            @include imgFit(contain);
            transition: opacity .5s;
          }
        }
      }
    }

    //友链声明
    .links-declaration {
      margin: 30px 0;
      padding: 10px 23px;
      background-color: $body-bg;
      border-left: 4px solid lighten($primary, 15%);
      display: flex;
      align-items: flex-start;

      .left {
        flex-shrink: 0;
        margin-right: 15px;

        .icon {
          font-size: 30px;
          color: lighten($primary, 15%);
        }
      }

      .right {
        min-width: 0;
        flex-grow: 1;

        .title {
          font-size: 16px;
          color: $title;
          font-weight: normal;
        }

        .subtitle {
          margin-top: 5px;
          font-size: 14px;
          color: $text-color;
        }
      }
    }
  }
}